import React, { useState, useEffect } from 'react';
import { app, pages } from '@microsoft/teams-js';
import ChatInterface from './components/ChatInterface';
import Header from './components/Header';
import StatusPanel from './components/StatusPanel';
import './App.css';
import './components/styles.css';
import './teams.css';

function App() {
  const [apiStatus, setApiStatus] = useState('checking');
  const [availablePdfs, setAvailablePdfs] = useState([]);
  const [selectedPdf, setSelectedPdf] = useState('harness_gear');
  const [ocrMethod, setOcrMethod] = useState('pymupdf');
  const [teamsInitialized, setTeamsInitialized] = useState(false);
  const [teamsContext, setTeamsContext] = useState(null);

  // Initialize Teams SDK
  useEffect(() => {
    const initializeTeams = async () => {
      try {
        await app.initialize();
        
        // Get Teams context
        const context = await app.getContext();
        setTeamsContext(context);
        setTeamsInitialized(true);
        
        // Notify Teams that the app has loaded successfully
        app.notifySuccess();
        
        console.log('Teams context:', context);
      } catch (error) {
        console.error('Failed to initialize Teams SDK:', error);
        // Still allow the app to work outside Teams
        setTeamsInitialized(true);
      }
    };

    initializeTeams();
  }, []);

  // Check API status on component mount
  useEffect(() => {
    if (teamsInitialized) {
      checkApiStatus();
    }
  }, [teamsInitialized]);

  const checkApiStatus = async () => {
    try {
      // In Teams, we might need to use a different API endpoint
      const apiUrl = process.env.REACT_APP_API_URL || '/health';
      const response = await fetch(apiUrl);
      if (response.ok) {
        const data = await response.json();
        setApiStatus('connected');
        setAvailablePdfs(data.available_pdfs || []);
      } else {
        setApiStatus('error');
      }
    } catch (error) {
      console.error('API health check failed:', error);
      setApiStatus('error');
    }
  };

  // Show loading state while Teams is initializing
  if (!teamsInitialized) {
    return (
      <div className="teams-loading">
        <div className="loading-spinner">Initializing Teams App...</div>
      </div>
    );
  }

  return (
    <div className="App teams-app">
      <Header 
        apiStatus={apiStatus} 
        onRefresh={checkApiStatus}
        teamsContext={teamsContext}
      />
      
      <div className="app-container">
        <StatusPanel 
          apiStatus={apiStatus}
          availablePdfs={availablePdfs}
          selectedPdf={selectedPdf}
          ocrMethod={ocrMethod}
          onPdfChange={setSelectedPdf}
          onOcrMethodChange={setOcrMethod}
          onRefresh={checkApiStatus}
        />
        
        <ChatInterface 
          selectedPdf={selectedPdf}
          ocrMethod={ocrMethod}
          apiStatus={apiStatus}
          teamsContext={teamsContext}
        />
      </div>
    </div>
  );
}

export default App;
